/* styles.css */

/* General Styles */

body {
    font-family: "Arial", sans-serif;
    margin: 0 20px;
    padding: 0;
  }
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  form {
    margin-bottom: 10px;
  }
  
  .input-label {
    margin-bottom: 5px;
  }
  
  input {
    width: 600px;
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  button {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .notification {
    font-size: 18px;
    margin-bottom: 10px;
    padding: 12px;
    border-radius: 4px;
  }
  
  .notification#red {
    background-color: #f8d7da;
    color: #721c24;
  }
  
  .notification#green {
    background-color: #d4edda;
    color: #155724;
  }
  
  /* Specific Styles */
  
  .filter-form {
    display: flex;
    align-items: center;
  }
  
  .filter-form input {
    padding: 8px;
    margin-right: 10px;
  }
  
  .person-form {
    display: flex;
    flex-direction: column;
  }
  
  .person-form input {
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .person-list {
    margin-top: 20px;
  }
  
  .person {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-radius: 4px;
    background-color: #f7f7f7;
  }
  
  .person-name {
    font-weight: bold;
  }
  
  .person-delete {
    padding: 6px 12px;
    background-color: #dc3545;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .person-delete:hover {
    background-color: #c82333;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }
  
  .input-field {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  